import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-numberpad',
  templateUrl: './numberpad.component.html',
  styles: []
})

export class NumberpadComponent implements OnInit {
  @Input() phoneNumber: string;
  @Output() phoneNumberChange = new EventEmitter();

  public isBackspaceHidden = true;

  constructor() { }

  ngOnInit() {
  }

  public press(key: number): void {
    if (key != null && key != undefined) {
      this.phoneNumber += key;
    }
    else {
      this.phoneNumber = this.phoneNumber.substring(0, this.phoneNumber.length - 1);
    }

    this.phoneNumberChange.emit(this.phoneNumber);
  }
}
